canvas{width:100%;height:100%;position:absolute;left:0;top:0}body{margin:0;background-color:black}.canvas-main-container,.canvas-minimap-container{position:relative}.canvas-main-container{width:100%;height:100%;border:5px solid #b78846;box-sizing:border-box}.wrapper{width:100%;margin-left:auto;margin-right:auto;box-sizing:border-box}.canvas-minimap-container{width:200px;height:120px;border:1px solid black;display:none}#editor-panel{height:170px;background-color:#202058;color:white}#toggle-editor{bottom:15px;left:15px;z-index:1;position:absolute;border-radius:100px;height:70px;width:70px;text-align:center;font-weight:bold;font-size:1rem;background-color:tan}#tiles{display:flex}#tiles .tile.active{border:3px solid white}#start{position:fixed;top:30%;left:50%;font-size:50px}.game-ui{box-sizing:border-box;background-color:#333333;border:1px solid white;padding:5px;width:20%;position:absolute;right:0;z-index:1;height:100%;color:white}.game-ui .col{width:120px;margin-right:50px}.game-ui .stats{position:absolute;top:0px;margin-left:auto;margin-right:auto;left:3%;color:white;font-size:1rem}.game-ui .bar{position:relative;height:20px;width:100%;border:1px solid white;background-color:black}.game-ui .bar__filled{height:100%}.game-ui .bar__filled.xp{background-color:white}.game-ui .bar__filled.health{background-color:#c30000}
